}

.table-bordered {
    border: 1px solid #dee2e6;
}

.table {
    width: 100%;
    margin-bottom: 1rem;
    background-color: transparent;
}

.table-striped tbody tr:nth-of-type(2n+1) {
    background-color: rgba(0, 0, 0, 0.05);
}

.table-striped tbody tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.05);
}

.table-custom td {
    color: rgb(188, 187, 185);
}

.table-custom td {
    color: #fff;
    padding-top: 20px;
    padding-bottom: 20px;
}

.table-bordered td,
.table-bordered th {
    border-color: rgb(41, 45, 46);
}

.table td,
.table th {
    border-top-color: rgb(41, 45, 46);
}

.table-bordered td,
.table-bordered th {
    border: 1px solid #dee2e6;
}

.table td,
.table th {
    padding: 0.75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
}
.text-center {
    text-align: center;
}
.large-gutters {
    margin-right: -35px;
    margin-left: -35px;
}
large-gutters > [class*="col-"] {
    padding-right: 35px;
    padding-left: 35px;
}
h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5 {
    color: rgb(188, 187, 185);
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
    font-size: 1.75rem;
}
.opacity-7 {
    opacity: .7;
}
a:hover {
    color: #27a86d !important;
    text-decoration: underline !important;
    text-underline-offset: 10px !important;
    text-decoration-color: #fff !important;
}
.fade-enter {
    opacity: 0;
    transform: translate(0, 25px);
    z-index: 1;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    transform: translate(0, 0);

    transition: opacity 250ms ease-out, transform 300ms ease;
}
.fade-exit {
    opacity: 1;
    transform: translate(0, 0);
}
.fade-exit.fade-exit-active {
    opacity: 0;
    transform: translate(0, 30px);

    transition: opacity 250ms ease-out, transform 300ms ease;
}